:root {
  --gray: hsl(0, 0%, 94%);
  --black: hsl(0, 0%, 10%);
  --dark: #332200;
  --white: #ffffff;
  --green: #009688;
}

body {
  display: flex;
  align-items: center;
  width: 46rem;
  margin: 0 auto;
  justify-content: center;
  border: 0.4rem solid var(--dark);
  background-image: linear-gradient(var(--gray) 1px, transparent 1px),
    linear-gradient(90deg, var(--gray) 1px, transparent 1px);
  background-size: 2rem 2rem;
  background-position: -0.1rem -0.1rem;
  font-family: "Patrick Hand", cursive;
  padding: 30px 0px;
  color: var(--black);
  font-size: 2rem;
}

h1 {
  margin: 0px;
  font-size: 6rem;
  letter-spacing: 0.3rem;
  display: flex;
  justify-content: center;
}

/* for description component */
.description {
  padding: 30px;
}

.btn-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn {
  background-color: transparent;
  color: var(--black);
  border: 1px solid var(--black);
  font-family: inherit;
  font-size: 1rem;
  border-radius: 5px;
  cursor: grab;
}

.btn:hover {
  box-shadow: rgba(0, 0, 0, 0.55) 1px 1px 2.6px;
}

/* for result component */
.result-wrapper {
  padding: 30px 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.result-wrapper div {
  display: flex;
  gap: 20px;
}

.result-wrapper .result {
  font-size: 3rem;
  padding-bottom: 30px;
}

.result-wrapper .answer {
  padding-bottom: 5px;
}

.result-wrapper .record {
  padding-bottom: 30px;
}

#footer {
  margin-top: 50px;
  font-size: 1.2rem;
  text-align: center;
}
a {
  color: var(--black);
}

@media only screen and (max-width: 768px) {
  body {
    width: 36rem;
    font-size: 1.6rem;
  }

  h1 {
    font-size: 5rem;
  }

  .result-wrapper .result {
    font-size: 2.25rem;
  }
}

@media only screen and (max-width: 580px) {
  body {
    width: 21rem;
    font-size: 1.4rem;
  }

  h1 {
    font-size: 4rem;
  }

  .result-wrapper .result {
    font-size: 2rem;
  }

  .btn {
    font-size: 0.9rem;
    border-radius: 5px;
    padding: 6px 18px;
  }

  #footer {
    margin-top: 20px;
  }
}
